import React  from "react";
import { useAuth } from "../hooks/useAuth";
import { useDispatch } from "react-redux";
import { useNavigate } from 'react-router-dom';
import {deleteUser} from "../redux/user/reducer";
import {Navbar, Nav, Button, Container} from  "react-bootstrap";
import {Link, NavLink} from  "react-router-dom";
import styled from "styled-components";
import logo from "../IMG/logo.png";
import cart from "../IMG/cart.png"

const Styles = styled.div`
    a, .navbar-brand, .navbar-nav, .nav-link {
        text-decoration: none;
        font-family: 'Montserrat';
        font-size: 20px;
        color: #adb2b4;
        transition: all linear .4s;
        &:hover{
            color: white;
        }
    .active{
        color:white; 
        }
    } 
`

const Navibar = () => {

    const {isAuth} = useAuth(); 
    const dispatch = useDispatch(); 
    const navigate = useNavigate();
    const logOut = () => {
        dispatch(deleteUser());
        navigate('/');
    }

    return(
            <Styles>
                <Navbar expand="md" collapseOnSelect bg="dark" variant="dark">
                    <Container className="sm-text-center justify-content-sm-center">
                    <Navbar.Brand href="/">
                        <img
                             src={logo}
                             width="70"
                             height="40"
                             className="d-inline-block align-top"
                             alt="React Bootstrap logo"
                        />
                    </Navbar.Brand>
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                        <Navbar.Collapse id="responsive-navbar-nav">
                             <Nav className="me-auto my-2 my-lg-0">
                                  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/"></NavLink></Nav.Link>
                                  {isAuth ?  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/catalog"></NavLink></Nav.Link> :  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/login"></NavLink></Nav.Link>}
                                  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/delivery"></NavLink></Nav.Link>
                                  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/payment"></NavLink></Nav.Link>
                                  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/servis"></NavLink></Nav.Link>
                                  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/reference"></NavLink></Nav.Link>
                                  <Nav.Link className="text-md-start text-center"><NavLink activeclassname="active" to="/comments"></NavLink></Nav.Link>
                             </Nav>
                             <Nav className="my-2">
                                 {isAuth ?  <Button variant="light" className="me-3 my-2 my-md-0"><Link style={{"color": "black"}} to="/cart"><img src={cart}/> </Link></Button> : <Button onClick={()=> navigate('/login')} variant="outline-light" className="me-3 my-2 my-md-0"><img src={cart}/></Button>}
                                 {isAuth ?  <Button variant="light" className="me-2 my-2 my-md-0" onClick={logOut}></Button>:<Button variant= "outline-light" className="me-2 my-2 my-md-0" onClick={()=> navigate('/login')}></Button>}
                             </Nav>
                        </Navbar.Collapse>
                      </Container>
                </Navbar>
            </Styles>
    )
}

export default Navibar;