import React,{useState} from "react";
import Subtitle from "../Components/Subtitle";
import {Questions} from "../Components/Questions"; 
import {Col, Container, Row, Modal, Form, Button} from  "react-bootstrap";
import {useNavigate} from  "react-router-dom";
import cls from "../Styles/Payment.module.scss";
import visa from "../IMG/Visa.webp";

const Payment = () => {

    const navigate = useNavigate();
    const [show, setShow] = useState(false);
    const [succes, setSucces] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const succesClose = () =>{
        setSucces(false);
        navigate('/delivery');
    } 
    const succesShow = () => {
        handleClose();
        setSucces(true);
    }

    return(
        <>
        <Subtitle title=' '/>
        <Container className="p-4" style={{overflow: "hidden"}}>
            <Row onClick={handleShow} className="align-items-center">
            <Col xs={5}><div className={cls.paymentImg}><img src={visa}/></div></Col>
                <Col xs={7}>
                    <h3 className={cls.paymentTitle}>  </h3>
                    <p className={cls.paymentText}>   .    ,     </p>
                </Col>
            </Row>
            <Row onClick={handleShow} className="align-items-center">
            <Col xs={5}><div className={cls.paymentImg}><img src={visa}/></div></Col>
                <Col xs={7}>
                    <h3 className={cls.paymentTitle}>-  </h3>
                    <p className={cls.paymentText}>       -   .</p>
                </Col>
            </Row>
            <Row onClick={handleShow} className="align-items-center">
            <Col xs={5}><div className={cls.paymentImg}><img src={visa}/></div></Col>
                <Col xs={7}>
                    <h3 className={cls.paymentTitle}>  </h3>
                    <p className={cls.paymentText}>         ,      .</p>
                </Col>
            </Row>
            <Modal show={show} onHide={handleClose}>
       <Modal.Header closeButton><Modal.Title> </Modal.Title></Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1">
              <Form.Label>   </Form.Label>
              <Form.Control as="textarea" rows={1} />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}></Button>
          <Button variant="primary" onClick={succesShow}></Button>
        </Modal.Footer>
      </Modal>
      <Modal show={succes} onHide={succesClose}>
        <Modal.Header closeButton><Modal.Title>!</Modal.Title></Modal.Header>
        <Modal.Body>      </Modal.Body>
        <Modal.Footer><Button variant="secondary" onClick={succesClose}></Button></Modal.Footer>
      </Modal>
        </Container>
        <Questions/>
        </>
    )
}

export default Payment;